import clsx from 'clsx';
import PropTypes from 'prop-types';
import React from 'react';

const Cherry = ({ size, color }) => {
  return <div className={clsx('cherry', `cherry--size-${size}`)} style={{ backgroundImage: `url('img/logo/cherry-${color}.svg')` }}></div>;
};

Cherry.propTypes = {
  size: PropTypes.oneOf(['default', 'small', 'big']),
  color: PropTypes.oneOf(['white', 'black', 'gradient']),
};

Cherry.defaultProps = {
  size: 'default',
  color: 'white',
};

export default Cherry;
