import clsx from 'clsx';
import PropTypes from 'prop-types';
import React from 'react';

const Logo = ({ size, color }) => {
  return <div className={clsx('logo', `logo--size-${size}`)} style={{ backgroundImage: `url('img/logo/logo-${color}.svg')` }}></div>;
};

Logo.propTypes = {
  size: PropTypes.oneOf(['default', 'small', 'big']),
  color: PropTypes.oneOf(['white', 'black', 'gradient']),
};

Logo.defaultProps = {
  size: 'default',
  color: 'white',
};

export default Logo;
