import clsx from 'clsx';
import PropTypes from 'prop-types';
import React from 'react';

const Button = ({ color, size, disabled, children, loading, onClick }) => {
  return (
    <button className={clsx('button', `button--color-${color}`, `button--size-${size}`)} aria-label="Button" onClick={onClick} disabled={disabled}>
      {loading ? <div className="button__loading">Loading...</div> : <div className="button__content">{children}</div>}
    </button>
  );
};

Button.propTypes = {
  size: PropTypes.oneOf(['default, big, xl']),
  color: PropTypes.oneOf(['light', 'dark']),
  text: PropTypes.string,
  loading: PropTypes.bool,
  disabled: PropTypes.bool,
  onClick: PropTypes.func,
};

Button.defaultProps = {
  onClick: function () {},
  text: 'Click me!',
  loading: false,
  disabled: false,
  color: 'light',
};

export default Button;
